<template>
  <v-container fluid>
    <v-data-table
      :headers="headers"
      :items="records"
      :search="search"
      class="elevation-2"
      :loading="loadingRecords"
      loading-text="Loading... Please wait"
    >
      <template v-slot:top>
        <div class="d-flex mx-4 pt-4 mb-4">
          <v-btn
            color="success"
            class="text-capitalize"
            :to="{ name: 'CreateTicket' }"
            :disabled="loadingRecords"
          >
            <v-icon class="mr-1">mdi-plus-box</v-icon>
            Add New
          </v-btn>
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            class="shrink"
            label="Search"
            outlined
            dense
            hide-details
            :disabled="loadingRecords"
          ></v-text-field>
        </div>
        <v-divider></v-divider>
      </template>

      <template v-slot:[`item.subject`]="{ item }">
        {{ item.subject }}
        <span class="grey--text">#{{ item.id }}</span>
      </template>

      <template v-slot:[`item.created_at`]="{ item }">
        {{ item.created_at | dateFormat }}
      </template>

      <template v-slot:[`item.action`]="{ item }">
        <div v-show="$vuetify.breakpoint.mdAndUp ? true : false">
          <v-tooltip left>
            <template v-slot:activator="{ on, attrs }" v-if="!item.assigned_to">
              <v-btn
                icon
                small
                color="primary"
                :to="{ name: 'EditTicket', params: { id: item.id } }"
                :disabled="loadingRecords"
              >
                <v-icon small color="primary" v-bind="attrs" v-on="on">
                  mdi-pencil
                </v-icon>
              </v-btn>
            </template>
            <span>Edit</span>
          </v-tooltip>

          <v-tooltip left>
            <template v-slot:activator="{ on, attrs }" v-if="!item.assigned_to">
              <v-btn
                icon
                small
                color="error"
                @click="deleteRecord(item.id)"
                :disabled="loadingRecords"
              >
                <v-icon small color="error" v-bind="attrs" v-on="on">
                  mdi-delete
                </v-icon>
              </v-btn>
            </template>
            <span>Delete</span>
          </v-tooltip>
        </div>

        <div v-show="$vuetify.breakpoint.mdAndUp ? false : true">
          <v-btn
            color="primary"
            x-small
            class="mr-1"
            :to="{ name: 'EditTicket', params: { id: item.id } }"
            :disabled="loadingRecords"
            v-if="!item.assigned_to"
          >
            Edit
          </v-btn>

          <v-btn
            color="error"
            x-small
            @click="deleteRecord(item.id)"
            :disabled="loadingRecords"
            v-if="!item.assigned_to"
          >
            Delete
          </v-btn>
        </div>
      </template>
    </v-data-table>
    <confirmation-message
      v-if="confirmationDialog"
      :confirmationData="confirmationData"
      @closeConfirmationDialog="closeConfirmationDialog"
      @onSubmit="onDelete"
    />
  </v-container>
</template>

<script>
import { mapState } from "vuex";
import moment from "moment";
import ConfirmationMessage from "@/components/ConfirmationMessage.vue";

export default {
  components: { ConfirmationMessage },

  data() {
    return {
      headers: [
        { text: "Subject", value: "subject" },
        { text: "Status", value: "ticket_status" },
        { text: "Date Created", value: "created_at" },
        { text: "Action", value: "action", sortable: false },
      ],
      search: "",
      dialog: false,
      userData: {},
      confirmationDialog: false,
      confirmationData: {
        title: "Are you sure you want to delete?",
        text: "This action cannot be undone.",
        closeBtnText: "Cancel",
        closeBtnColor: "gray",
        saveBtnText: "Delete",
        saveBtnColor: "error",
      },
      id: null,
    };
  },

  created() {
    this.$store.commit("myTicket/setRecords", []);
    this.$store.dispatch("myTicket/getRecords");
  },

  computed: {
    ...mapState("myTicket", ["loadingRecords", "records"]),
  },

  methods: {
    deleteRecord(id) {
      this.confirmationDialog = true;
      this.id = id;
    },

    async onDelete() {
      this.confirmationDialog = false;
      await this.$store.dispatch("myTicket/deleteRecord", this.id);
    },

    closeConfirmationDialog() {
      this.confirmationDialog = false;
    },
  },

  filters: {
    dateFormat(date) {
      return moment(date).format("MMM D, YYYY h:mm A");
    },
  },
};
</script>
